.plus-app.plus-app-2017-10-verolaskuri {
  /* For old browsers */
  display: block;
  margin-bottom: 20px;
  /* General styles */
}
@media all and (-webkit-min-device-pixel-ratio: 0) {
  .plus-app.plus-app-2017-10-verolaskuri select,
  .plus-app.plus-app-2017-10-verolaskuri textarea,
  .plus-app.plus-app-2017-10-verolaskuri input {
    font-size: 16px;
    padding: 5px;
  }
}
.plus-app.plus-app-2017-10-verolaskuri h1 {
  font-size: 30px;
  font-weight: bold;
}
.plus-app.plus-app-2017-10-verolaskuri button {
  border: 0;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  font-size: .875em;
  font-weight: 300;
  height: 31px;
  line-height: 31px;
  margin-top: 10px;
  padding: 0 20px;
  width: auto;
}
.plus-app.plus-app-2017-10-verolaskuri button.active {
  background-color: #11b579;
  border-radius: 2px;
  cursor: pointer;
}
.plus-app.plus-app-2017-10-verolaskuri .form_container {
  margin-bottom: 20px;
}
.plus-app.plus-app-2017-10-verolaskuri .form_container .input_container {
  position: relative;
  margin: 20px 0;
}
.plus-app.plus-app-2017-10-verolaskuri .form_container .input_container input {
  border: 1px solid #ccc;
  width: 200px;
}
.plus-app.plus-app-2017-10-verolaskuri .form_container .input_container .label_container {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 10px;
}
.plus-app.plus-app-2017-10-verolaskuri .form_container .input_container .easy-autocomplete {
  position: relative;
}
.plus-app.plus-app-2017-10-verolaskuri .form_container .input_container .easy-autocomplete-container {
  background-color: #ffffff;
  box-sizing: border-box;
  margin-top: 5px;
  position: absolute;
  width: 100%;
  z-index: 30;
}
.plus-app.plus-app-2017-10-verolaskuri .form_container .input_container .easy-autocomplete-container ul,
.plus-app.plus-app-2017-10-verolaskuri .form_container .input_container .easy-autocomplete-container li {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.plus-app.plus-app-2017-10-verolaskuri .form_container .input_container .easy-autocomplete-container li {
  cursor: pointer;
  padding: 5px 5px;
}
.plus-app.plus-app-2017-10-verolaskuri .form_container .input_container .easy-autocomplete-container li:hover {
  background-color: #ccc;
}
.plus-app.plus-app-2017-10-verolaskuri .result_container h1,
.plus-app.plus-app-2017-10-verolaskuri .result_container h3 {
  margin-bottom: 20px;
  font-weight: bold;
}
.plus-app.plus-app-2017-10-verolaskuri .result_container h1 {
  font-size: 26px;
  line-height: 34px;
  margin-top: 30px;
}
.plus-app.plus-app-2017-10-verolaskuri .result_container h3 {
  font-size: 26px;
}
.plus-app.plus-app-2017-10-verolaskuri .result_container .value {
  font-weight: bold;
}
.plus-app.plus-app-2017-10-verolaskuri .result_container p {
  font-size: 16px;
  line-height: 24px;
  margin-bottom: 10px;
}
.plus-app.plus-app-2017-10-verolaskuri .result_container .more_container {
  margin-bottom: 20px;
}
.plus-app.plus-app-2017-10-verolaskuri .result_container .meta_container {
  display: none;
}
@media all and (max-width: 400px) {
  .plus-app.plus-app-2017-10-verolaskuri .result_container .meta_container {
    font-size: 12px;
  }
}
@media all and (min-width: 400px) {
  .plus-app.plus-app-2017-10-verolaskuri .result_container .meta_container {
    font-size: 14px;
  }
}
.plus-app.plus-app-2017-10-verolaskuri .result_container .meta_container h3 {
  margin-bottom: 15px;
}
.plus-app.plus-app-2017-10-verolaskuri .result_container .meta_container div {
  line-height: 24px;
}
.plus-app.plus-app-2017-10-verolaskuri .result_container .meta_container div .value {
  float: right;
}
.plus-app.plus-app-2017-10-verolaskuri .result_container .meta_container div.year_income {
  border-bottom: 1px solid #ccc;
}
.plus-app.plus-app-2017-10-verolaskuri .result_container .meta_container div.effective_income {
  border-top: 1px solid #ccc;
}
.plus-app.plus-app-2017-10-verolaskuri .result_container .meta_container div.deduction .value:before {
  content: '-';
}
.plus-app.plus-app-2017-10-verolaskuri .result_container .meta_container div.work_income_deduction {
  margin-top: 10px;
}
.plus-app.plus-app-2017-10-verolaskuri .result_container .neighbors_container {
  margin-top: 30px;
}
.plus-app.plus-app-2017-10-verolaskuri .result_container .neighbors_container table {
  box-sizing: border-box;
  width: 100%;
}
.plus-app.plus-app-2017-10-verolaskuri .result_container .neighbors_container table th {
  font-size: 13px;
  font-weight: bold;
  letter-spacing: 0.05em;
  text-align: left;
  text-transform: uppercase;
  white-space: nowrap;
}
@media all and (max-width: 400px) {
  .plus-app.plus-app-2017-10-verolaskuri .result_container .neighbors_container table th {
    font-size: 12px;
  }
}
@media all and (min-width: 400px) {
  .plus-app.plus-app-2017-10-verolaskuri .result_container .neighbors_container table th {
    font-size: 13px;
  }
}
.plus-app.plus-app-2017-10-verolaskuri .result_container .neighbors_container table tbody tr:nth-child(2n-1) {
  background-color: #eff0f4;
}
.plus-app.plus-app-2017-10-verolaskuri .result_container .neighbors_container table tbody tr td:last-child {
  font-weight: bold;
}
@media all and (max-width: 400px) {
  .plus-app.plus-app-2017-10-verolaskuri .result_container .neighbors_container table tbody tr td {
    font-size: 12px;
  }
}
@media all and (min-width: 400px) {
  .plus-app.plus-app-2017-10-verolaskuri .result_container .neighbors_container table tbody tr td {
    font-size: 14px;
  }
}
@media all and (min-width: 600px) {
  .plus-app.plus-app-2017-10-verolaskuri .result_container .neighbors_container table th,
  .plus-app.plus-app-2017-10-verolaskuri .result_container .neighbors_container table td {
    padding: 10px 20px;
  }
}
@media all and (max-width: 600px) {
  .plus-app.plus-app-2017-10-verolaskuri .result_container .neighbors_container table th,
  .plus-app.plus-app-2017-10-verolaskuri .result_container .neighbors_container table td {
    padding: 10px 10px;
  }
}
.plus-app.plus-app-2017-10-verolaskuri .result_container .neighbors_container table th.number,
.plus-app.plus-app-2017-10-verolaskuri .result_container .neighbors_container table td.number {
  text-align: right;
}
.plus-app.plus-app-2017-10-verolaskuri .clearfix {
  zoom: 1;
}
.plus-app.plus-app-2017-10-verolaskuri .clearfix:after {
  clear: both;
  content: ' ';
  display: block;
  height: 0;
  visibility: hidden;
}
.plus-app.plus-app-2017-10-verolaskuri .clear_both {
  clear: both;
}
.plus-app.plus-app-2017-10-verolaskuri .hidden {
  display: none;
}
.plus-app.plus-app-2017-10-verolaskuri .nowrap {
  white-space: nowrap;
}
